import {
    takeLatest,
    takeEvery
} from 'redux-saga';
import {
    take,
    call,
    put,
    fork,
    cancel,
    select
} from 'redux-saga/effects';
import uuid from 'uuid';
import FlowyTree from '../common/FlowyTree';
import M from 'immutable';
import flowyService from '../services/flowy';
var app = require('electron').remote;
var dialog = app.dialog;
const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms))
    /**
     * [*getTree description]
     * @param {[type]} action        [description]
     * @yield {[type]} [description]
     */
function* getTree(store) {
    let flowyData = yield call(flowyService.getFlowy);
    let tree = FlowyTree.createTree(flowyData);
    let treeView = FlowyTree.getVisibleTreeView(tree, flowyData.rootId);
    yield put({
        type: 'flowy/get/success',
        payload: M.Map({
            rootId: flowyData.rootId,
            tree: tree,
            treeView: treeView,
            addons: flowyData.addons
        })
    });
}

function* watchTreeModified() {
    while (true) {
        yield take([
            'flowy/item/update/content', 
            'flowy/item/add', 
            'flowy/item/delete', 
            'flowy/item/tab', 
            'flowy/item/shiftTab', 
            'flowy/item/update/toggle-open', 
            'flowy/pop',
            'flowy/item/update/property'
        ]);
        const store = yield select((store) => {
            return store.get('flowy');
        });
        yield call(flowyService.saveFlowy, store.toJS());
    }
}

function* watchTreeGet() {
    yield takeEvery('flowy/get', getTree);
}

function* watchDownload() {
    yield takeEvery('download', function*() {
        // alert('download')
        dialog.showSaveDialog(function(fileName) {
            if (fileName === undefined) {
                console.log("You didn't save the file");
                return;
            }
            const content = flowyService.exportAppData();
            fs.writeFile(fileName, content, function(err) {
                if (err) {
                    alert("An error ocurred creating the file " + err.message)
                }
                alert("The file has been succesfully saved");
            });
        });
    })
}

function* watchUpload() {
    yield takeEvery('upload', function* () {
        dialog.showOpenDialog(function(fileNames) {
            // fileNames is an array that contains all the selected
            if (fileNames === undefined) {
                console.log("No file selected");
            } else {
                readFile(fileNames[0]);
            }
        });

        function readFile(filepath) {
            fs.readFile(filepath, 'utf-8', function(err, data) {
                if (err) {
                    alert("An error ocurred reading the file :" + err.message);
                    return;
                }

                try {
                    data = JSON.parse(data);
                    if (!data['angrytask-tree']) {
                        throw new Error('file format error');
                    }
                    flowyService.importAppData(data);
                    setTimeout(function() {
                        window.location.reload();
                    }, 100);
                } catch(e) {
                    console.log('e')
                }
            });
        }
    })
}
export default function*() {
    yield fork(watchDownload);
    yield fork(watchUpload);
    yield fork(watchTreeGet);
    yield fork(watchTreeModified);
    yield put({
        type: 'flowy/get'
    });
    yield put({
        type: 'flowy/get/a'
    })
}


