/**
 * [groupByScrum description]
 * @param  {[type]} tasks [description]
 * @return {[type]}       [description]
 */
export function groupByScrum(tasks) {
	let groups = {};
	tasks = tasks || [];

	groups.default = tasks.filter((it) => {
		const type = it.get('scrum')
		return !type || type === 'default';
	});

	groups.backlog = tasks.filter((it) => {
		return it.get('scrum') === 'backlog'
	});

	groups.sprint = tasks.filter((it) => {
		return it.get('scrum') === 'sprint'
	});

	groups.product = tasks.filter((it) => {
		const type = it.get('scrum')
		return type === 'product';
	});;
	return groups;
}

/**
 * [search description]
 * @param  {[type]} tasks   [description]
 * @param  {[type]} keyword [description]
 * @param  {Array}  tags    [description]
 * @return {[type]}         [description]
 */
export function searchTask(tasks, keyword, tags = []) {
	return tasks.filter(t => {
		return t.content.indexOf(keyword) >= 0
	})
}

/**
 * [groupByDefault description]
 * @param  {[type]} tasks [description]
 * @return {[type]}       [description]
 */
export function groupByPriority(tasks) {
	let groups = {};

	const types = ['completed', 'default', 'warning', 'error', 'danger'];

	types.forEach((t) => {
		groups[t] = [];
	})

	tasks.forEach((t) => {
		// 完成的分为一组
		if (t.get('progress') === 100) {
			return groups.completed.push(t);
		}

		let priority = t.get('priority');
		if (types.indexOf(priority) < 0) {
			priority = 'default';
		}
		return groups[priority].push(t);
	});

	let priority = 'default';
	types.shift();
	types.forEach(type => {
		if (groups[type].length > 0) {
			priority = type;
		}
	})
	groups.priority = priority;

	return groups;
}

/**
 * [activeTasks description]
 * @param  {[type]} tasks [description]
 * @return {[type]}       [description]
 */
export function activeTasks(tasks) {
	return tasks.filter((t) => {
		return t.get('progress') !== 100;
	})
}

/**
 * [completedTasks description]
 * @param  {[type]} tasks [description]
 * @return {[type]}       [description]
 */
export function completedTasks(tasks) {
	return tasks.filter((t) => {
		return t.get('progress') === 100;
	})
}


