// @flow
import React from 'react';
// root component
import App from './App';
// redux
import { Provider } from 'react-redux';

type RootType = {
  store: {}
};

export default function Root({ store }: RootType) {
  return (
    <Provider store={store}>
			<App />
		</Provider>
  );
}


