/**
 * tree structure
 *
 *  // root node id = 'root'
 *  // parentNode.children -> someNode -> nextNode -> nextNode ...
 *  Object {
 *  	nodeId: {
 *  		id: UUId,
 *  		content: String,
 *  		
 *  		// children in list structure
 *  		children: UUID, 
 *
 * 			// point to next brother
 *  		next: UUID,
 *
 * 			// parent id
 * 			pid: UUID,
 *
 * 			// open or close
 *  		open: boolean,
 *  	},
 *  	nodeId2: {...}
 *  }
 */
import M from 'immutable';
/**
 * [createTree description]
 * @param  {[type]} data [description]
 * @return {[type]}      [description]
 */
function createTree(rawData) {
    let data = JSON.parse(JSON.stringify(rawData));
    let hash = data.tree;
    return M.fromJS(hash);
}

/**
 * [getVisibleTreeView description]
 * @return {[Array]} [description]
 */
function getVisibleTreeView(tree, rootId) {
    let ret = [];
    dfs(tree, rootId, (node, depth) => {
        ret.push({
            node,
            depth
        });
        if (node.get('open') || node.get('id') === rootId) {
            return true;
        }
        return false;
    });
    return M.fromJS(ret);
}

/**
 * [insert insert a node to another node]
 * @param  {[type]} tree     [description]
 * @param  {[type]} parentId [description]
 * @param  {[type]} newNode  [description]
 * @return {[type]}          [description]
 *
 *  parent.children -> head -> someNode -> adjacentTo -> newNode -> next
 */
function insert(tree, newNode, parentId, adjacentTo) {
    if (adjacentTo) {
        // set newNode.next = next.id
        newNode = newNode.set('next', adjacentTo.get('next'));
        // set adjacentTo.next = newNode.id
        tree = tree.set(adjacentTo.get('id'), adjacentTo.set('next', newNode.get('id')));
    } else {
        // set newNode.next = currentChildren.id
        newNode = newNode.set('next', tree.getIn([parentId, 'children']));
        // set currentNode.children = newNode.id;
        tree = tree.setIn([parentId, 'children'], newNode.get('id'));
    }
    tree = tree.set(newNode.get('id'), newNode);
    return tree;
}
/**
 * [remove a node]
 * @param  {[type]} tree   [description]
 * @param  {[type]} nodeId [description]
 * @return {[type]}        [description]
 *
 *  (beforeNode | parent) -> node -> nextNode
 */
function remove(tree, nodeId) {
	const node = tree.get(nodeId);
    const pid = node.get('pid');
    const parent = tree.get(pid);
    const brothers = getChildren(tree, parent);
    const index = brothers.indexOf(node);
    if (index === 0) {
        // if node is head 
        // set parent.children = node.next
        const nextId = brothers[1] ? brothers[1].get('id') : null;
        tree = tree.setIn([pid, 'children'], nextId);
    } else {
        // if node is not head
        // set beforeNode 
        let beforeNode = brothers[index - 1];
        beforeNode = beforeNode.set('next', node.get('next'));
        tree = tree.set(beforeNode.get('id'), beforeNode);
    }
    // delete currentNode
    tree = tree.delete(node.get('id'));
    return tree;
}
