/**
 * [dfs depth first search tree ]
 * @param  {[type]} tree    [description]
 * @param  {[type]} rootId  [description]
 * @param  {[type]} visitor [description]
 * @return {[type]}         [description]
 */
function dfs(tree, rootId, visitor) {
    let stack = [];
    let depth = 0;
    stack.push(tree.get(rootId).set('depth', 0));
    while (stack.length > 0) {
        let current = stack.pop();
        let currentDepth = current.get('depth');
        if (!visitor(current, currentDepth)) {
            continue
        }
        let children = getChildren(tree, current);
        if (children) {
            depth++;
            for (let i = children.length - 1; i >= 0; i--) {
                let child = children[i];
                child = child.set('depth', currentDepth + 1);
                stack.push(child);
            }
        }
    }
}

/**
 * [children description]
 * @param  {[type]} tree   [description]
 * @param  {[type]} headId [description]
 * @return {[type]}        [description]
 */
function getChildren(tree, node) {
    let headId = node.get('children');
    let ret = [];
    let cur = tree.get(headId);
    while (cur) {
        ret.push(cur);
        cur = tree.get(cur.get('next'));
    }
    return ret;
}

/**
 * [getPath description]
 * @param  {[type]} tree   [description]
 * @param  {[type]} nodeId [description]
 * @return {[type]}        [description]
 */
function getPath(tree, nodeId) {
    let current = tree.get(nodeId);
    let path = [];
    while (current && current.get('id') !== 'root') {
        path.unshift(current);
        current = tree.get(current.get('pid'));
    }
    path.unshift(tree.get('root'));
    return path;
}

export default {
    createTree,
    getVisibleTreeView,
    dfs,
    remove,
    insert,
    getPath,
    getChildren
};


