import React from 'react';
import {searchTask} from "../../common/taskFilter";
export default class SearchResult extends React.Component {
	render() {
		const flowy = this.props.flowy;
		const tree = flowy.get("tree").toJS();
		const tasks = Object.keys(tree).map(key => tree[key])
		const filterTasks = searchTask(tasks, flowy.get("search"));
		return (
			<div className="app-left-search-result block content">
				{this.renderTasks(filterTasks)}
			</div>
		)
	}

	renderTasks(tasks){
		if (tasks.length === 0) {
			return (
				<div className="empty">
					找到 0 个结果
				</div>
			)
		}
		return (
			<div className="found-tasks-list">
				{tasks.map(task => {
					return (
						<div className="found-task-item fadeInUp animated" key={task.id} onClick={ev => {
							this.props.dispatch("flowy/search", {
								keyword: ""
							});
							this.props.dispatch("flowy/pop", {
								id: task.id
							})
						}}>
							<span className="dot"></span> 
							<span className="content">{task.content}</span>
						</div>
					)
				})}
			</div>
		)
	}
}


