import React, { Component } from 'react';
import {Checkbox} from 'antd';
export default class Hashtag extends Component {
	constructor() {
		super(arguments);
		this.onCheck = (ev) => {
			this.props.onCheck(ev.target.checked)
		}
	}

	render() {
		const props = this.props
		return (
		  <label { ...props } className="task-editor__checkbox">
		  	<Checkbox defaultChecked={this.props.task.model.model.checked} onChange={this.onCheck}/>
		  </label>
		);
	}
}



