import React, { Component } from "react";
import { findDOMNode } from "react-dom";
import PropTypes from "prop-types";
import flow from "lodash/flow";

const ItemTypes = { TAG: "tag" };

function RemoveComponent(props) {
  if (props.readOnly) {
    return <span />;
  }

  if (props.removeComponent) {
    const Component = props.removeComponent;
    return <Component {...props} />;
  }

  return (
    <a onClick={props.onClick} className={props.className}>
      {String.fromCharCode(215)}
    </a>
  );
}

class Tag extends Component {
  render = () => {
    const { props } = this;
    const label = props.tag;
    const {
      isDragging,
      readOnly,
      CustomRemoveComponent,
    } = props;

    const tagComponent = (
      <span
        style={{ opacity: isDragging ? 0 : 1 }}
        className={props.classNames.tag}>
        {label}
        <RemoveComponent
          className={props.classNames.remove}
          removeComponent={props.removeComponent}
          onClick={props.onDelete}
          readOnly={props.readOnly}
        />
      </span>
    );
    return tagComponent;
  };
}

Tag.PropTypes = {
  labelField: PropTypes.string,
  onDelete: PropTypes.func.isRequired,
  tag: PropTypes.object.isRequired,
  moveTag: PropTypes.func,
  removeComponent: PropTypes.func,
  classNames: PropTypes.object,
  readOnly: PropTypes.bool,
  isDragging: PropTypes.bool.isRequired,
};

Tag.defaultProps = {
  labelField: "text",
  readOnly: false,
};

export default Tag


