import React from 'react';
import Node from './Node';
import { AutoSizer, VirtualScroll } from 'react-virtualized'
import shallowCompare from 'react-addons-shallow-compare'
import {focusTo} from '../../components/FlowyEditor'; 
import uuid from 'uuid';
import {Icon} from 'antd';

const Flowy = React.createClass({
    displayName: 'Flowy',
    shouldComponentUpdate(nextProps, nextState) {
        return shallowCompare(this, nextProps, nextState);
    },
    componentDidUpdate(prevProps, prevState) {
        // this.$VirtualScroll.forceUpdateGrid();  
    },
    render() {
    	const flowy = this.props.flowy;

    	if (!flowy) {
    		return this.renderLoading();
    	}

    	const treeView = flowy.get('treeView');
    	 
        return (
            <div className="workflowy-tree">
                {this.renderRootNode(treeView.get('0'))}
            	{this.renderList(treeView)}
            </div>
        );
    },
    renderLoading() {
    	return (
    		<div className="workflowy-tree">
    			loading ...
    		</div>
    	)
    },
    renderRootNode(node) {
        return (
            <div className="root">
                {this.renderNode({
                    index: 0,
                    isRoot: true
                })}
            </div>
        )
    },
    renderList(list) {
        let $list = [];
        list = list || [];

        if (list.size <= 1) {
            return this.renderPlus(list.get(0));
        }

        list.forEach((it, index) => {
            if (index > 0) {
                $list.push(this.renderNode({
                    index
                }));
            }
        });

        return $list;
    },
    renderPlus(rootNode) {
        return (
            <div className="plusone">
                <Icon type="plus" onClick={this.newLine.bind(null, rootNode.get('node'))}/>
            </div>
        )
    },
    renderVirtualList(list) {
    	const visibleNodes = list.size;
        const nodeHeight = 30;
    	return (
    	   <AutoSizer>
            {({width, height}) => (
              <VirtualScroll
                width={width}
                height={height}
                rowCount={visibleNodes}
                rowHeight={nodeHeight}
                overscanRowCount={3}
                rowRenderer={this.renderNode}
                // force update virtualScroll after props changed
                force={list}
                ref={(co) => {
                    this.$VirtualScroll = co;
                }}
              />
            )}
          </AutoSizer>
        )
    },
    renderNode(virtualNode) {
        const treeView = this.props.flowy.get('treeView');
        const focusedId = this.props.flowy.get('focusedId') || this.props.flowy.get("rootId");
        const it = treeView.get(virtualNode.index);

        if (!it) {
            return 'null';
        }

        const node = it.get('node');
        const depth = it.get('depth');

        const focused = node.get('id') === focusedId;

    	return <Node 
                    dispatch={this.props.dispatch}
                    depth={depth} 
                    node={node}
                    index={virtualNode.index}
                    root={virtualNode.isRoot}
                    key={node.get('id')}
                    onDeleteNode={this.onDeleteNode}
                    newLine={this.newLine}
                    focused={focused}
                    focusUp={this.focusUp}
                    focusDown={this.focusDown}
                    />
    },
    /**
     * [onDeleteNode description]
     * @param  {[type]} node [description]
     * @return {[type]}      [description]
     */
    onDeleteNode(node, index) {
        const treeView = this.props.flowy.get('treeView');
        const focusToNode = treeView.get(index - 1);
        this.focusTo(focusToNode.get('node').get('id'));
    },
    /**
     * [newLine description]
     * @param  {[type]} node [description]
     * @return {[type]}      [description]
     */
    newLine(node) {
        const dispatch = this.props.dispatch;

        const newNode = {
            id: uuid(),
            content: ''
        }

        dispatch('flowy/item/add', {
            node,
            newNode
        });

        this.focusTo(newNode.id);
    },
    /**
     * [focusUp description]
     * @param  {[type]} node  [description]
     * @param  {[type]} index [description]
     * @return {[type]}       [description]
     */
    focusUp(node, index) {
        const treeView = this.props.flowy.get('treeView');
        if (index === 0) {
            return;
        } 
        this.focusTo(treeView.getIn([index - 1, 'node', 'id']));
    },
    /**
     * [focusDown description]
     * @param  {[type]} node  [description]
     * @param  {[type]} index [description]
     * @return {[type]}       [description]
     */
    focusDown(node, index) {
        const treeView = this.props.flowy.get('treeView');
        const size = treeView.size;
        if (index == size - 1) {
            return;
        } 
        this.focusTo(treeView.getIn([index + 1, 'node', 'id']));
    },
    /**
     * [focustTo description]
     * @param  {[type]} id [description]
     * @return {[type]}    [description]
     */
    focusTo(id) {
        focusToEditor(id);
    }
});

const EDITOR_SELECTOR = ' .task-editor .public-DraftEditor-content';

/**
 * [focusToEditor description]
 * @param  {[type]} id [description]
 * @return {[type]}    [description]
 */
function focusToEditor(id) {
    setTimeout(function() {
        // focus to new node 
        var $newNodeEditor = $('#workflowy-' + id + EDITOR_SELECTOR);
        focusTo($newNodeEditor[0])
    }, 10)
}

export default Flowy;


