/**
 * [inline editing]
 * @param  {Object} config [description]
 * @return {[type]}        [description]
 */
import {getDefaultKeyBinding, KeyBindingUtil} from 'draft-js';
const {hasCommandModifier} = KeyBindingUtil;

function nil(){}
const inlineaddon = (config = {}) => {
	const ENTER_COMMAND = 'inline-key-enter';
	const DELETE_COMMAND = 'delete-command';

	const onReturn = config.onReturn || nil;
	const deleteNode = config.onDelete || nil;

	// 移动操作
	const tab = config.tab || nil;
	const shiftTab = config.shiftTab || nil;
	const moveUp = config.moveUp || nil;
	const moveDown = config.moveDown || nil;

	return {
		keyBindingFn: (ev) => {
			// console.log(ev.keyCode);
			/**
			 * [key: delete]
			 * @param  {[type]} ev.keyCode [description]
			 * @return {[type]}            [description]
			 */
			if (ev.keyCode === 8) {
				const editorState = config.editor.state.editorState;
				const text = editorState.getCurrentContent().getPlainText()
				if (text === '') {
					deleteNode();
				}
			}

			if (ev.ctrlKey || ev.shiftKey) {

				// ctrl + [ || h
				if (ev.keyCode === 219 || ev.keyCode === 72) {
					shiftTab();
					return {};
				}

				// ctrl + j
				if (ev.keyCode === 74) {
					moveDown();
					return {};
				}

				// ctrl + k
				if (ev.keyCode === 75) {
					moveUp();
					return {};
				}

				// ctrl + ] || l
				if (ev.keyCode === 221 || ev.keyCode === 76) {
					tab();
					return {};
				}
				
			}

			return getDefaultKeyBinding(ev);
		},
		handleReturn: (ev) => {
			onReturn && onReturn();
			return true;
		},
		handleKeyCommand: (command) => {
			return false
		}
	}
}

export default inlineaddon


