/**
 * todos reducer
 */
import { createReducer, combineReducers} from 'redux-immutablejs'
import M , {fromJS} from 'immutable';
import uuid from 'uuid';
import FlowyTree from '../common/FlowyTree';

/**
 * [intialState]
 */
const intialState = M.Map({
	// current focusedId
	focusedId: null,
	// current root
	rootId: 'root',
	// tree -> nodeMap
	tree: null,
	// current tree view
	treeView: null,
	// search keyword
	search: ""
});

const reducers = {
	["flowy/search"](state, action){
		return state.set("search", action.payload.keyword);
	},
	/**
	 * add new item
	 */
	['flowy/item/add'](state, action) {
		const payload = action.payload;
		let {
			node,
			newNode
		} = payload;

		let adjacentTo = null;
		let pid = node.get('pid') || state.get('rootId');

		const nodeId = node.get('id');
		const open = node.get('open');

		// if node have children and node is opened
		// insert node as first child
		if (open || nodeId === state.get('rootId')) {
			pid = nodeId;
		} else {
			adjacentTo = node;
		}

		// create new Node
		newNode.pid = pid;
		newNode = M.fromJS(newNode);

		state = state.update("tree", (tree) => {
			return FlowyTree.insert(tree, newNode, pid, adjacentTo);
		});
		
		return updateTreeView(state);
	},
	/**
	 * delete node
	 */
	['flowy/item/delete'](state, action) {
		const payload = action.payload;
		let {
			node			
		} = payload;

		const children = node.get('children');

		if (children) {
			return;
		}

		let tree = state.get('tree');
		state = state.set('tree', FlowyTree.remove(tree, node.get('id')));

		return updateTreeView(state) ;
	},
	/**
	 * common method for update property
	 */
	['flowy/item/update/property'](state, action) {
		const payload = action.payload;
		const {
			node,
			property,
			value
		} = payload;

		state = state.updateIn(['tree', node.get('id')], (node) => {
			return node.set(property, fromJS(value));
		});

		return updateTreeView(state) ;
	},
	/**
	 * update item content
	 */
	['flowy/item/update/content'](state, action) {
		const payload = action.payload;
		const {
			id,
			value
		} = payload;

		state = state.updateIn(['tree', id], (node) => {
			return node.set('content', value);
		});

		return updateTreeView(state) ;
	},
	/**
	 * toggle open
	 */
	['flowy/item/update/toggle-open'](state, action) {
		const {
			node
		} = action.payload;

		state = state.updateIn(['tree', node.get('id')], (node) => {
			let open = !node.get('open');
			return node.set('open', open);
		});

		return updateTreeView(state);
	},
	/**
	 * focus
	 */
	['flowy/item/focus'](state, action) {
		return state.set('focusedId', action.payload.id);
	},
	/**
	 * tab
	 */
	['flowy/item/tab'](state, action) {
		const treeView = state.get('treeView');
		const tree = state.get('tree');

		let {
			node,
			index
		} = action.payload;

		const nodeId = node.get('id');
		const nodeNextId = node.get('next');
		const nodePid = node.get('pid');
		const preNode = treeView.getIn([index - 1, 'node']);

		node = tree.get(nodeId);

		/**
		 * 1）如果该节点是父亲的第一个孩子节点，不做处理
		 * 同 preNode === parentNode
		 * 第一种情况是第二种的特殊情况
		 *
		 * - a
		 * 		- b (tab)
		 */ 
		if (preNode.get('id') === nodePid) {
			return state;
		}

		if (preNode.get('next') === nodeId) {
			state = case2();
		}
		
		if (preNode.get('next') !== nodeId) {
			state = case3();
		}

		return updateTreeView(state);

		/**
		 * 2) 如果上一个节点的 next === 当前节点 
		 * 将 b 挂在 a 节点的 children 上
		 *  - a 
		 *  - b (tab)
		 *  - c 
		 *
		 * 变为 
		 *
		 *  - a 
		 *  	- b 
		 *  - c
		 */
		function case2() {
			const nodeA = preNode;
			const children = FlowyTree.getChildren(tree, nodeA);
			const lastChild = children.pop();

			if (lastChild) {
				// node->next = null
				// node->pid = nodeA.id
				// lastChild->next = node.id
				// nodeA->next = node->next
				return state
						.setIn(['tree', nodeId, 'next'], null)
						.setIn(['tree', nodeId, 'pid'], nodeA.get('id'))
						.setIn(['tree', lastChild.get('id'), 'next'], nodeId)
						.setIn(['tree', nodeA.get('id'), 'next'], nodeNextId)
						.setIn(['tree', nodeA.get('id'), 'open'], true);
			} else {
				// nodeA->children = node.id
				// node->next = null
				// node->pid = nodeA.id
				// nodeA->next = node->next
				return state
						.setIn(['tree', nodeId, 'next'], null)
						.setIn(['tree', nodeId, 'pid'], nodeA.get('id'))
						.updateIn(['tree', nodeA.get('id')], (node)=> {
							return node
									.set('children', nodeId)
									.set('next', nodeNextId)
									.set('open', true)
						});
			}
		}

		/**
		 * 3) 如果上一个节点是 open 的 
		 *  找到节点
		 *  - a 
		 *  	- a1
		 * 		- a2
		 * 			- a21
		 * 			- a22
		 *  - b 
		 *  - c
		 */
		function case3() {

			let nodeA = null;
			for (let i = index; i >= 0; i--) {
				let nodeI = treeView.getIn([i, 'node']);
				if (nodeI.get('next') === node.get('id')) {
					nodeA = nodeI;
				}
			}

			if (nodeA) {
				const children = FlowyTree.getChildren(tree, nodeA);
				const lastChild = children.pop();
				// last-child->next = node
				// node->next = null;
				// node->id = nodeA.id;
				return state
						.setIn(['tree', nodeId, 'next'], null)				
						.setIn(['tree', nodeId, 'pid'], nodeA.get('id'))				
						.setIn(['tree', lastChild.get('id'), 'next'], nodeId)
						.setIn(['tree', nodeA.get('id'), 'next'], nodeNextId);
			}

			return state;
		}
	},
	/**
     * shift + tab 
	*/
	['flowy/item/shiftTab'](state, action) {
		let {
			node,
			index,
			depth
		} = action.payload;

		const treeView = state.get('treeView');
		const tree = state.get('tree');
		const nodeId = node.get('id');
		const nodeNextId = node.get('next');
		const nodePid = node.get('pid');
		const preNode = treeView.getIn([index - 1, 'node']);

		/**
		 * 1) depth 为第一级的情况
		 * - a 
		 * - b (shift tab)
		 */
		if (depth <= 1) {
			return state;
		}

		if (preNode.get('id') === nodePid) {
			state = case2();
		} else {
			state = case3();
		}

		return updateTreeView(state);

		/**
		 * 2) b 作为第一个孩子节点
		 *
		 *  - a
		 *  	- b (shift tab)
		 *  	- a1
		 *  - c 
		 *
		 *  变为 
		 *  
		 *  - a 
		 *  	- a1
		 *  - b
		 *  - c
		 */
		function case2() {
			const nodeA = preNode;
			// b->next = a->next
			// b->pid = a->pid
			// a->next = b 
			// a->children = b->next 
			return state
					.setIn(['tree', nodeId, 'next'], nodeA.get('next'))
					.setIn(['tree', nodeId, 'pid'], nodeA.get('pid'))
					.updateIn(['tree', nodeA.get('id')], (node) => {
						node = node
							.set('children', nodeNextId)
							.set('next', nodeId)
						if (!nodeNextId) {
							node = node.set('open', false);
						}
						return node;
					});
		}

		/**
		 * 3) b 在孩子节点中间
		 *  - a
		 *  	- a1
		 *  	- b (shift tab)
		 *  	- a2
		 *  - c
		 */
		function case3() {
			// findNodeA 
			const nodeA = tree.get(nodePid);
			// find nodeA1
			let nodeA1 = preNode;

			for (let i = index; i >= 0; i--) {
				let nodeI = treeView.getIn([i, 'node']);
				if (nodeI.get('next') === node.get('id')) {
					nodeA1 = nodeI;
				}
			}

			// b->next = a->next
			// b->pid = a->pid
			// a->next=b
			// a1->next = b->next
			
			return state
					.setIn(['tree', nodeId, 'next'], nodeA.get('next'))
					.setIn(['tree', nodeId, 'pid'], nodeA.get('pid'))
					.setIn(['tree', nodeA.get('id'), 'next'], nodeId)
					.setIn(['tree', nodeA1.get('id'), 'next'], nodeNextId)
		}
	},
	/**
	 * moveUp
	 */
	['flowy/item/moveUp'](state, action) {
		const {
			node,
			index
		} = action.payload;

		const treeView = state.get('treeView');
		const tree = state.get('tree');
		const nodeId = node.get('id');
		const nodeNextId = node.get('next');
		const nodePid = node.get('pid');
		const nodeParent = tree.get(nodePid);
		const preNode = treeView.getIn([index - 1, 'node']);

		/**
		 * 0) index === 1 
		 * - b
		 * 
		 */
		if (index <= 1) {
			return state;
		}

		const pre2Node = treeView.getIn([index - 2, 'node']);

		// - pre2Node
		// - preNode
		// - b
		// - nextNode
		// to
		// - pre2Node
		// 	- preNode 
		// - node
		// - nextNode 

		// node -> next = preNode.id
		// node -> pid = preNode.pid
		state = state.updateIn(['tree', nodeId], (node) => {
			return node
				.set('next', preNode.get('id'))
				.set('pid', preNode.get('pid'))
		})

		// if (pre2Node->children === preNode.id) {
		// 	pre2Node -> children = node
		// } else {
		// 	preNodePreBrother->next = node
		// }
		if (pre2Node.get('children') === preNode.get('id')) {
			state = state.setIn(['tree', pre2Node.get('id'), 'children'], nodeId);
		} else {
			for (let i = index - 2; i >= 0; i--) {
				let nodeI = treeView.getIn([i, 'node']);
				if (nodeI.get('next') === preNode.get('id')) {
					state = state.setIn(['tree', nodeI.get('id'), 'next'], nodeId);
				}
			}
		}

		// if (preNode.get('id') === nodePid) {
		// 	nodeParent->children = node->next
		// } else {
		// 	if (children[i].next === node.id) {
		// 		children[i].next = node->next
		// 	}
		// }

		if (preNode.get('id') === nodePid) {
			state = state.setIn(['tree', preNode.get('id'), 'children'], nodeNextId);
			if (!nodeNextId) {
				state = state.setIn(['tree', preNode.get('id'), 'open'], false)
			}
		} else {
			for (let i = index - 1; i >= 0; i--) {
				let nodeI = treeView.getIn([i, 'node']);
				if (nodeI.get('next') === nodeId) {
					state = state.setIn(['tree', nodeI.get('id'), 'next'], nodeNextId);
				}
			}
		}

		return updateTreeView(state);
	},
	/**
	 * moveUp
	 */
	['flowy/item/moveDown'](state, action) {
		const {
			node,
			index
		} = action.payload;
		const treeView = state.get('treeView');
		const tree = state.get('tree');
		const nodeId = node.get('id');
		const nodeNextId = node.get('next');
		const nodePid = node.get('pid');
		const nodeParent = tree.get(nodePid);
		const size = treeView.size;

		if (index >= size - 1) {
			return state;
		}
		// debugger

		const preNode = treeView.getIn([index - 1, 'node']);
		const nextNode = treeView.getIn([index + 1, 'node']);
		const nodeDepth = node.get('depth');

		/**
		 * 
		 *  - somenode
		 *   	- nodePreBrother
		 * 		 	- preNode
		 *      - b
		 *      	- bchildNode
		 *  - targetNode
		 *  	- next2Node
		 *
		 *  if (preNode.id === nodePid) {
		 *  	preNode->children = nodeNextId
		 *  } else {
		 *  	nodePreBrother->next = node->next
		 *  }
		 *  
		 *  if (targetNode.open && targetNode.children) {
		 *  	node->next = targetNode.children;
		 *  	node->parent = targetNode.id;
		 *  	targetNode->children = node.id
		 *  } else {
		 *  	node->next = targetNode.next;
		 *  	node->parent = targetNode.pid
		 *  	taretNode->next = node.id
		 *  }
		 */
		
		let nodePreBrother = null, i, nodeI;
		if (preNode.get('id') === nodePid) {
			state = state.setIn(['tree', preNode.get('id'), 'children'], nodeNextId)
			if (!nodeNextId) {
				state = state.setIn(['tree', preNode.get('id'), 'open'], false)
			}
		}  else {
			// find node pre brother
			for (i = index - 1; i >= 0; i--) {
				nodeI = treeView.getIn([i, 'node']);
				if (nodeI.get('next') === nodeId) {
					state = state.setIn(['tree', nodeI.get('id'), 'next'], nodeNextId)
					break;
				}
			}
		}

		// find target node 
		let targetNode = null;
		for (i = index + 1; i < size; i++) {
			nodeI = treeView.getIn([i, 'node']);
			if (nodeI.get('depth') <= nodeDepth) {
				targetNode = nodeI;
				break;
			}
		}

		if (targetNode.get('open') && targetNode.get('children')) {
			state = state
				.setIn(['tree', nodeId, 'next'], targetNode.get('children'))
				.setIn(['tree', nodeId, 'pid'], targetNode.get('id'))
				.setIn(['tree', targetNode.get('id'), 'children'], nodeId);
		} else {
			state = state
				.setIn(['tree', nodeId, 'next'], targetNode.get('next'))
				.setIn(['tree', nodeId, 'pid'], targetNode.get('pid'))
				.setIn(['tree', targetNode.get('id'), 'next'], nodeId);
	
		}

		return updateTreeView(state);
	},
	/**
	 * pop
	 */
	['flowy/pop'](state, action) {
		const {
			id
		} = action.payload;

		state = state
			.set('rootId', id)
			.set('focusedId', id);

		return updateTreeView(state);
	},
	/**
	 * initial tree
	 */
	['flowy/get/success'](state, action) {
		return action.payload;
	},
};

/**
 * [updateTreeView description]
 * @param  {[type]} state [description]
 * @return {[type]}       [description]
 */
function updateTreeView(state) {
	const tree = state.get('tree');
	const rootId = state.get('rootId');
	state = state.set('treeView', FlowyTree.getVisibleTreeView(tree, rootId));
	return state;
}

export default createReducer(intialState, reducers);


