import React from 'react';
import findTasks from '../../common/findTasks';
import {groupByScrum} from '../../common/taskFilter';
import {Icon} from "antd";

const TabKanban = React.createClass({
	render() {
		const {
			tree,
			task
		} = this.flatProps();

		const tasks = findTasks(tree, task.get('id'), true);
		const taskGroups = groupByScrum(tasks);

		return (
			<div className="tasky-tab-kanban">
				<div style={{
					width: 1084,
					height: "100%"
				}}>
					<div className="backlog">
						<div className="inner">
							<div className="title"> <i class='anticon anticon-calendar'></i> 计划 </div>
							<div className="content">
								{this.renderGroup(taskGroups.backlog, 'backlog')}
								<div className="divider">
									其他								
								</div>
								{this.renderGroup(taskGroups.default, 'default')}
							</div>
						</div>
					</div>
					<div className="sprint">
						<div className="inner">
							<div className="title"> 🏃 冲刺 </div>
							<div className="content">
								{this.renderGroup(taskGroups.sprint, 'sprint')}
							</div>
						</div>
					</div>
					<div className="product">
						<div className="inner">
							<div className="title"> ✅ 完成 </div>
							<div className="content">
								{this.renderGroup(taskGroups.product, 'product')}
							</div>
						</div>
					</div>
				</div>
			</div>
		)
	},
	flatProps() {
		const props = this.props;
		const flowy = props.flowy;
		const focusedId = flowy.get("focusedId");
		const rootId = flowy.get("rootId");

		const tree = flowy.get("tree");
		const task = tree.get(focusedId) || tree.get(rootId);

		return {
			task,
			tree
		};
	},
	renderGroup(arr, name) {
		arr = arr || [];

		let icon;
		switch (name) {
			case 'backlog':
				icon = 'arrow-right';
				break;
			case 'sprint':
				icon = "arrow-right";
				break;
			case 'product':
				icon = "arrow-left";
				break; 
			default:
				icon = "arrow-up";
				break;
		}

		return (
			<ul className={`kanban-task-group kanban-task-group-${name}`}>
				{arr.map((it) => {
					return <Item 
						task={it} 
						icon={icon} 
						key={`kanban-item-${it.get('id')}`}
						name={name}
						onClick={this.onClickItem}
						onStep={this.onStep}/>
				})}
			</ul>
		)
	},
	onStep(task, name, forward) {
		const dispatch = this.props.dispatch;
		let scrumNext = 'backlog';
		const arr = ['default', 'backlog', 'sprint', 'product'];
		const index = arr.indexOf(name) || 0;

		if (forward) {
			if (index === arr.length - 1) {
				scrumNext = arr[arr.length - 2];
			} else {
				scrumNext = arr[index + 1];
			}
		} else {
			if (index === 0) {
				scrumNext = 'default';
			} else {
				scrumNext = arr[index - 1];
			}
		}

		dispatch('flowy/item/update/property', {
			node: task,
			property: 'scrum',
			value: scrumNext
		});
	},
	onClickItem(node) {
		const dispatch = this.props.dispatch;

		dispatch('tasky/changeTab', {
			tab: 'TabTask'
		});

		dispatch('flowy/pop', {
			id: node.get('id')
		})
	}
});

function Item(props) {
	const {
		task,
		icon,
		name
	} = props;
	return (
		<li className="kanban-task-item">
			<a onClick={() => {
				props.onClick(task);
			}}>{task.get('content')} </a>
			<span>
				<Icon type={icon} onClick={()=> {
					props.onStep(task, name, true)
				}}/>
			</span>
		</li>
	)
}

export default TabKanban;


