import React from 'react';
import TodoList from './TodoList';
import {Icon, Tag, Radio, Row, Col} from 'antd';
import ClassNames from 'classnames';

function Toggle(props){
	const open = props.open;
	const onClickHandler = props.onChange || function(){};

	let $icon = <Icon type="caret-right"/>;
	if (open) {
		$icon = <Icon type="caret-down"/>
	}

	return (
		<span className="toggle" onClick={onClickHandler}>
			{$icon}
		</span>
	)
}

export default Toggle;



