import CheckBox from './CheckBox';
import checkboxStrategy from './checkboxStrategy';
import decorateComponentWithProps from 'decorate-component-with-props';

const checkedTaskaddon = (config = {}) => {
	return {
		decorators: [{
			strategy: checkboxStrategy,
			component: decorateComponentWithProps(CheckBox, {task: config.task, onCheck: config.onCheck})
		}]
	}
}

export default checkedTaskaddon;


