/**
 * App Component
 */
import React from "react";
import M from "immutable";
import { connect } from "react-redux";
// import FormGenerator from './FormGenerator';
import { Button, Icon, Modal, Tabs } from "antd";
import Flowy from "../Flowy";
import Bread from "../Flowy/Bread";
import LeftHeader from "./LeftHeader";
import AppMenu from "./Tabs";
import Playground from "./Playground";
import SearchResult from "../SearchResult";
const TabPane = Tabs.TabPane;

const App = React.createClass({
  getInitialState() {
    return {
      showSettings: false
    };
  },
  componentDidMount() {
    this.split();
  },
  componentDidUpdate() {
    this.split();
  },
  split() {
    if (!this.props.flowy.get("treeView")) {
      return;
    }

    if (!$(".app-body .gutter")[0]) {
      Split(["#AppLeft", "#AppRight"], {
        sizes: [30, 70],
        minSize: [260, 200]
      });
    }
  },
  render() {
    if (!this.props.flowy.get("treeView")) {
      return <div>loading</div>;
    }

    const search = this.props.flowy.get("search") || ""
    let $leftBody = null 

    if (!!search.trim()) {
     $leftBody = <SearchResult {...this.props}/> 
    } else {
      $leftBody = [
        (<div key="nav" className="app-left-footer block content shrink">
          <Bread {...this.props} />
        </div>),
        (<div key="main" className="app-left-main block content">
          <Flowy {...this.props} />
        </div>)
      ]
    }

    return (
      <div className="app-container">
        <div className="app-body split">
          <div
            className="app-left split-horizontal block vertical"
            id="AppLeft"
          >
            <LeftHeader {...this.props} />
            {$leftBody}
            <div className="block content shrink">
              <div className="settings"> 
                <span onClick={ev => {
                  this.setState({
                    showSettings: true
                  })
                }}><Icon type="setting"/>设置</span>
              </div>
            </div>
          </div>
          <div className="app-right split-horizontal" id="AppRight">
            <div className="app-right-header drag-region block">
              <div className="block content" />
              <div className="block shrink">
                <AppMenu {...this.props} />
              </div>
              <div className="block" />
            </div>
            <div className="app-right-main">
              <Playground {...this.props} />
            </div>
          </div>
        </div>

        <Modal
          title={<span><Icon type="setting"/> 应用设置 </span>}
          visible={this.state.showSettings}
          footer={null}
          onOk={ev => {
            this.setState({
              showSettings: false
            })
          }}
          onCancel={ev => {
            this.setState({
              showSettings: false
            })
          }}
          >
            <Tabs tabPosition="top">
              <TabPane tab="版本更新" key="1">
                当前版本为 0.0.1 
                <Button ghost type="primary" size="small" style={{marginLeft: 10}}> 检查更新 </Button>
              </TabPane>
              <TabPane tab="导入导出" key="2">
                <Button ghost type="primary" size="small"> 导入 </Button>
                <Button ghost type="primary" size="small" style={{marginLeft: 10}}> 导出 </Button>
              </TabPane>
              <TabPane tab="License" key="3">
                购买
              </TabPane>
            </Tabs>
          </Modal>
      </div>
    );
  }
});

/**
 * transform store data to App props
 * @param  {[type]} state [description]
 * @return {[type]}       [description]
 */
const mapStateToProps = state => {
  return {
    flowy: state.get("flowy"),
    tasky: state.get("tasky")
  };
};

/**
 * add dispatch to 
 * @param  {[type]} dispatch [description]
 * @return {[type]}          [description]
 */
const mapDispatchToProps = dispatch => {
  return {
    dispatch: (type, payload) => {
      dispatch({
        type,
        payload
      });
    }
  };
};

/**
 * create redux container
 * @type {[type]}
 */
const AppContainer = connect(mapStateToProps, mapDispatchToProps)(App);

export default AppContainer;



