import React from 'react';
import {Icon, Tag, Radio, Row, Col, Switch, Tooltip} from 'antd';
import ClassNames from 'classnames';

const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;

const Menu = React.createClass({
	render() {
		const tasky = this.props.tasky;
		const tab = tasky.get('tab') || 'TabTask';
		return (
			<RadioGroup 
				value={tab} 
				size="small" 
				onChange={this.onChange}>
	      <RadioButton value="TabTask">任务</RadioButton>
	      <RadioButton value="TabKanban">看板</RadioButton>
	      <RadioButton value="TabCalendar">日历</RadioButton>
	    </RadioGroup>
		) 
	},
	onChange(ev) {
		const value = ev.target.value;
		const dispatch = this.props.dispatch;
		dispatch('tasky/changeTab', {
			tab: value
		})
	}
});

export default Menu;


