import React from 'react';
import {Icon} from 'antd';
import FlowyEditor, {focusTo} from '../../components/FlowyEditor';	
import uuid from 'uuid';
import classNames from 'classnames';
import shallowCompare from 'react-addons-shallow-compare'

const Node = React.createClass({
	shouldComponentUpdate(nextProps, nextState) {
		return shallowCompare(this, nextProps, nextState)
	},
	componentDidUpdate(prevProps, prevState) {
	},
	render() {
		const props = this.props;
		const node= props.node;

		let $toggle = null;
		let $tab = null;
		let $bullet = null;

		const cls = classNames({
			'workflowy-tree-node': true, 
			'block': true,
			'focused': props.focused,
            "fadeInUp": true,
            "animated": true
		});

		if (!props.root) {
			$tab = this.renderTab(props.depth);

			const bulletClass = classNames({
				bullet: true,
				fat: node.get('children') && !node.get('open')
			});

			$toggle = <div className="block shrink">
						{this.renderToggle()}
						<a className={bulletClass} 
							draggable={true}
							onDragStart={this.onDragStartBullet.bind(null, node)} 
							onDragEnd={this.onDragEndBullet.bind(null, node)}
							onDrag={this.onDragBullet.bind(null, node)}
							onClick={this.pop.bind(null, node)}></a>
					</div>
		}

		return (
			<div className={cls} id={`workflowy-${node.get('id')}`}>
				{$tab}
				{$toggle}
				<div className="name block content">
					<FlowyEditor
						task={node}
						onChange={this.onChangeNodeValue} 
						onDelete={this.onDeleteNode}
						newLine={this.newLine}
						onCheck={this.onCheck}
						onFocus={this.onFocus}
						tab={this.tab}
						shiftTab={this.shiftTab}
						moveUp={this.moveUp}
						moveDown={this.moveDown}
						focusUp={this.focusUp}
						focusDown={this.focusDown}
						/>
					{/*<div className="extra-info">
						<span className="chip">任务</span>
					</div>*/}
				</div>
			</div>
		)
	},
	/**
	 * [renderTab description]
	 * @param  {[type]} depth [description]
	 * @return {[type]}       [description]
	 */
    renderTab(depth) {
    	depth -= 1;
    	return <div className="tabs block shrink" style={{width: depth * 30}}> </div>
    },
    /**
     * [renderToggle description]
     * @return {[type]} [description]
     */
    renderToggle() {
    	const node = this.props.node;
    	const children = node.get('children');
    	const open = node.get('open');

    	if (!children || children.size <= 0) {
    		return null;
    	}

    	let $icon = <Icon type="plus"/>;

    	if (open) {
    		$icon = <Icon type="minus"/>;
    	}

    	return (
    		<span
				className="toggle" 
				onClick={this.onToggleNode}>
				{$icon}
			</span>
		)
    },
    /**
     * [onChangeNodeValue description]
     * @param  {[type]} node  [description]
     * @param  {[type]} value [description]
     * @return {[type]}       [description]
     */
	onChangeNodeValue(node, value) {
		const dispatch = this.props.dispatch;
		dispatch('flowy/item/update/content', {
			id: node.get('id'),
			value
		});
	},
	/**
	 * [onDeleteNode description]
	 * @return {[type]} [description]
	 */
	onDeleteNode(node) {
		const dispatch = this.props.dispatch;

		// cant delete first node
		if (this.props.index === 0) {
			return;
		}

		const children = node.get('children');

		if (!children) {
			dispatch('flowy/item/delete', {
				node
			});

			this.props.onDeleteNode(node, this.props.index);
		}

	},
	/**
	 * [onFocus description]
	 * @return {[type]} [description]
	 */
	onFocus() {
		const dispatch = this.props.dispatch;
		dispatch('flowy/item/focus', {
			id: this.props.node.get('id')
		})
	},
	/**
	 * [newLine description]
	 * @param  {[type]} node [description]
	 * @return {[type]}      [description]
	 */
	newLine(node) {
		this.props.newLine(node);
	},
	/**
	 * [onToggleNode description]
	 * @return {[type]} [description]
	 */
    onToggleNode() {
		const dispatch = this.props.dispatch;
    	const node = this.props.node;

    	dispatch('flowy/item/update/toggle-open', {
    		node
    	});
    },
    /**
     * [pop description]
     * @return {[type]} [description]
     */
    pop() {
    	const node = this.props.node;
		const dispatch = this.props.dispatch;
		
		dispatch('flowy/pop', {
			id: node.get('id')
		});
    },
    /**
     * [tab description]
     * @return {[type]} [description]
     */
    tab() {
    	const {
    		node,
    		index,
    		dispatch
    	} = this.props;

    	dispatch('flowy/item/tab', {
    		node,
    		index
    	});
    },
    /**
     * [shiftTab description]
     * @return {[type]} [description]
     */
    shiftTab() {
    	const {
    		node,
    		index,
    		depth,
    		dispatch
    	} = this.props;

    	dispatch('flowy/item/shiftTab', {
    		node,
    		index,
    		depth
    	});
    },
    /**
     * [moveUp description]
     * @return {[type]} [description]
     */
    moveUp() {
    	const {
    		node,
    		index,
    		dispatch
    	} = this.props;

    	dispatch('flowy/item/moveUp', {
    		node,
    		index
    	});
    },
    /**
     * [moveDown description]
     * @return {[type]} [description]
     */
    moveDown() {
    	const {
    		node,
    		index,
    		dispatch
    	} = this.props;

    	dispatch('flowy/item/moveDown', {
    		node,
    		index
    	});
    },
    /**
     * [focusUp description]
     * @return {[type]} [description]
     */
    focusUp() {
    	this.props.focusUp(this.props.node, this.props.index);
    },
    /**
     * [focusDown description]
     * @return {[type]} [description]
     */
    focusDown() {
    	this.props.focusDown(this.props.node, this.props.index);
    },
    /**
     * [onCheck description]
     * @return {[type]} [description]
     */
	onCheck() {

	},
	/**
	 * [onDragStartBullet description]
	 * @return {[type]} [description]
	 */
    onDragStartBullet() {

    },
    /**
     * [onDragEndBullet description]
     * @return {[type]} [description]
     */
    onDragEndBullet() {

    },
    /**
     * [onDragBullet description]
     * @return {[type]} [description]
     */
    onDragBullet() {

    },
});

export default Node;


