
import findWithRegex from '../utils/findWithRegex';

// const HASHTAG_REGEX = /\#[\w\u0590-\u05ff]+/g;
const HASHTAG_REGEX = /\#[^\s#]+/g;

export default (contentBlock, callback) => {
	findWithRegex(HASHTAG_REGEX, contentBlock, callback);
};



