import React from 'react';
import TodoList from './TodoList';
import Toggle from './Toggle';
import {Icon, Tag, Radio, Row, Col, Input, DatePicker, InputNumber, Slider, Menu, Dropdown} from 'antd';
import ClassNames from 'classnames';
import RichEditor from '../../components/FlowyEditor/rich';
import TaskChecker from "./TaskChecker";
import InputTags from "../../components/InputTags";
import moment from 'moment';
const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;
import {fromJS} from "immutable";

class Detail extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			showStartTimeDatePicker: false,
			showEndTimeDatePicker: false,
			showInputTags: false,
			asd: 1
		}
	}

	render() {
		return (
			<div className="task-detail">
				{this.renderRoot()}
				{this.renderMeta()}
			</div>
		)		
	}

	renderRoot() {
		const { task } = this.props;

		const content = task.get("content");

		const completed = task.get("progress") === 100;

		const menu = (
			<Menu theme="dark">
				<Menu.Item key="finish">
					<Icon type="check-circle-o" /> 完成项目
				</Menu.Item>
				<Menu.Item key="time">
					<a onClick={ev => {
						this.setState({
							showStartTimeDatePicker: true
						})
					}}>
						<Icon type="calendar" /> 开始日期
					</a>
				</Menu.Item>
				<Menu.Item key="deadline">
					<a onClick={ev => {
						this.setState({
							showEndTimeDatePicker: true
						})
					}}>
						<Icon type="flag" /> 截止日期
					</a>
				</Menu.Item>
				<Menu.Item key="tags">
					<a onClick={ev => {
						this.setState({
							showInputTags: true
						})
					}}>
						<Icon type="tag-o" /> 添加标签
					</a>
				</Menu.Item>
				<Menu.Divider />
				{/*<Menu.Item key="move">
					移动项目
				</Menu.Item>
				<Menu.Item key="copy">
					复制项目
				</Menu.Item>
				<Menu.Item key="repeat">
					重复计划
				</Menu.Item>*/}
				<Menu.Item key="delete">
					<Icon type="delete"/> 删除项目
				</Menu.Item>
				<Menu.Item key="export">
					<Icon type="export"/> 导出项目
				</Menu.Item>
			</Menu>
		);

		return (
			<div className={`tasky-root-node ${completed ? "completed" : ""}`}>
				<TaskChecker
					type={task.get("priority")}
					checked={completed}
					onClick={this.checkedRootNode}
				/>
				<span className="content">
					{content}
				</span>

				<Dropdown overlay={menu} trigger={["click"]}>
					<a className="ant-dropdown-link more" href="#">
						<Icon type="ellipsis" />
					</a>
				</Dropdown>
			</div>
		);
	}

	renderMeta() {
		const {
			task
		} = this.props;

		const taskData = task.toJS();
		let showStartTimeDatePicker = this.state.showStartTimeDatePicker;
		let $startTime = null;

		const dateFormat = 'YYYY[年] MMM Do';
		if (!!taskData.planStartTime || showStartTimeDatePicker) {
			$startTime = (
				<Row>
					<span>
						<Icon type="calendar"/> 开始日期: 
						<DatePicker 
							size="small"
							defaultValue={moment(new Date, dateFormat)}
							value={ taskData.planStartTime ? moment(taskData.planStartTime, dateFormat) : null }
							format={dateFormat}
							open={showStartTimeDatePicker}
							onChange={time => {
								this.props.dispatch("flowy/item/update/property", {
									node: task,
									property: "planStartTime",
									value: time ? moment(time).format(dateFormat) : 0
								});
								
							}}
							onOpenChange={status => {
								this.setState({
									showStartTimeDatePicker: status
								})
							}}
						/>
					</span>
					{/*<span> <Icon type="bell"/> 上午 9点 </span>*/}
				</Row>
			)
		}

		let showEndTimeDatePicker = this.state.showEndTimeDatePicker;
		let $endTime = null;

		if (!!taskData.planEndTime || showEndTimeDatePicker) {
			let $restDay = null;

			if (taskData.planEndTime) {
				$restDay = moment(taskData.planEndTime, dateFormat).toNow(true)
			}

			$endTime = (
				<Row>
					<span>
						<Icon type="calendar"/> 截止日期: 
						<DatePicker 
							size="small"
							defaultValue={moment(new Date, dateFormat)}
							value={ taskData.planEndTime ? moment(taskData.planEndTime, dateFormat) : null }
							format={dateFormat}
							open={showEndTimeDatePicker}
							onChange={time => {
								this.props.dispatch("flowy/item/update/property", {
									node: task,
									property: "planEndTime",
									value: time ? moment(time).format(dateFormat) : 0
								});
								
							}}
							onOpenChange={status => {
								this.setState({
									showEndTimeDatePicker: status
								})
							}}
						/>
					</span>
					<span style={{color: "#999"}}> 剩余 {$restDay}</span>
				</Row>
			)
		}

		let showInputTags = this.state.showInputTags;
		let $tags = null;

		if ((!!taskData.tags && taskData.tags.length > 0) || showInputTags) {

			const tags = taskData.tags || []

			$tags = (
				<Row>
					<InputTags
						tags={tags}
						suggestions={["play", "funk", "fumm", "fuasdfas", "fuasdf"]}
						placeholder="添加标签"
						handleDelete={i => {
							const newTags = tags.filter((tag, index) => {
								return index != i
							});

							this.props.dispatch("flowy/item/update/property", {
								node: task,
								property: "tags",
								value: fromJS(newTags)
							});
						}}

	          handleAddition={tag => {
	          	if (tags.indexOf(tag) >= 0) {
	          		return
	          	}

							this.props.dispatch("flowy/item/update/property", {
								node: task,
								property: "tags",
								value: [...tags, tag]
							});
	          }}

	          handleDrag={ev => {}}
					/>
				</Row>
			)
		}

		return (
			<div className="tasky-root-node-detail">
				<div title="计划安排" className="tasky-detail-section tasky-detail__plan">
					{$startTime}
					{$endTime}
					{$tags}
				</div>
				<div title="描述" className="tasky-detail-section">
					<RichEditor
						content={task.get('description')}
						placeholder="备注"
						onChange={(value) => {
							this.onChangeProperty('description', value);
						}}
					/>
				</div>
			</div>
		)
	}

	/**
	 * [onChangeProperty description]
	 * @param  {[type]} property [description]
	 * @param  {[type]} value    [description]
	 * @return {[type]}          [description]
	 */
	onChangeProperty(property, value) {
		const {
			task,
			dispatch
		} = this.props;

		dispatch('flowy/item/update/property', {
			node: task,
			property,
			value
		});
	}

	/**
	 * checked rootNode
	 */
	checkedRootNode() {
		const { task } = this.flatProps();

		const { dispatch } = this.props;

		let progress = 100;
		if (task.get("progress") === 100) {
			progress = 0;
		}

		dispatch("flowy/item/update/property", {
			node: task,
			property: "progress",
			value: progress
		});
	}
}

export default Detail;


