import { createReducer, combineReducers} from 'redux-immutablejs'
import M from 'immutable';

const initialState = M.fromJS({
	tab: 'TabTask',
	todos: {},
	ui: {
		showStartTimeDatePicker: false
	}
})

const reducers = {
	'tasky/changeTab': (state, action) => {
		const {
			tab
		} = action.payload;
		return state.set('tab', tab);
	},
	"tasky/ui": (state, action) => {
		const ui = state.get("ui").toJS();
		for (let attr in action.payload) {
			ui[attr] = action.payload[attr]
		}
		return state.set("ui", fromJS(ui))
	}
}

export default createReducer(initialState, reducers);


