import moment from 'moment';
import findTasks from '../../common/findTasks';

const mockEvents = [
  {
    'title': 'All Day Event',
    'allDay': true,
    'start': new Date(2015, 3, 0),
    'end': new Date(2015, 3, 0)
  },
  {
    'title': 'Conference',
    'start': new Date(2015, 3, 11),
    'end': new Date(2015, 3, 13),
    desc: 'Big conference for important people'
  },
  {
    'title': 'Birthday Party',
    'start':new Date(2015, 3, 13, 7, 0, 0),
    'end': new Date(2015, 3, 13, 10, 30, 0)
  }
]

export function getEvents(props) {
  const flowy = props.flowy;
  const focusedId = flowy.get('focusedId');
  const rootId = flowy.get('rootId');
  const tree = flowy.get('tree');
  const rootNode = tree.get(focusedId) || tree.get(rootId);
  let tasks = findTasks(tree, rootNode.get('id'), true)
  tasks = tasks.filter(t => {
    return !!t.get("planStartTime")
  })
  return getSheduledTasks(tasks);
} 

function getSheduledTasks(tasks) {
  let ret = [];

  tasks.forEach((task) => {
    const startTime = task.get('planStartTime');
    let endTime = task.get('planEndTime');

    if (startTime) {
      let event = {
        _task: task,
        id: task.get('id'),
        title: task.get('content'),
        start: moment(startTime, 'yyyy-MM-DD HH:mm:ss')._d
      }

      if (endTime) {
        endTime = moment(endTime, 'yyyy-MM-DD HH:mm:ss')._d;
      } 

      if (!endTime || endTime.getTime() < event.start.getTime()) {
        endTime = moment(event.start).add(1, 'hours')._d;
      }

      event.end = endTime;

      ret.push(event);
    }
  });

  return ret;
}



