import React from 'react';
import TaskDetail from '../TaskDetail';
import TaskKanban from '../TaskKanban';
import TaskCalendar from '../TaskCalendar';

const Tabs = {
	TabTask: TaskDetail,
	TabKanban: TaskKanban,
	TabCalendar: TaskCalendar
}

const Playground = React.createClass({
	render() {
		const props = this.props;
		const tasky = props.tasky;
		const Tab = Tabs[tasky.get('tab')] || Tabs.TabTask;
		return (
			<div className="addon-playground addon-tasky" style={{height: "100%"}}>
				{<Tab {...this.props}/>}
			</div>
		) 
	}
});

export default Playground;


