import React from 'react';
import findTasks from '../../common/findTasks';
import {groupByPriority, completedTasks, activeTasks} from '../../common/taskFilter';
import TaskChecker from './TaskChecker';

/**
 * [createClass description]
 * @param  {[type]} {	render( [description]
 * @return {[type]}            [description]
 */
const TodoList = React.createClass({
	getInitialState() {
	    return {
	        hideCompleted: true
	    };
	},
	render() {
		const props = this.props;
		return (
			<div className="tasky-todo-list">
				{this.renderTasks()}
			</div>
		)
	},
	renderTasks() {
		let tasks = this.findChildTasks();

		if (tasks.length === 0) {
			return null;
		}

		const groups = groupByPriority(tasks);
		const totalCount = tasks.length;
		const completedCount = groups.completed.length;

		const priority = groups.priority;

		let toggleLabel = "隐藏";
		if (this.state.hideCompleted) {
			tasks = activeTasks(tasks);
			toggleLabel = "显示已完成";
		}

		return (
			<div className={`group`}>
				<div className="group-title">
					<span className="title">子任务</span> 
					<small> 已完成 <b>{completedCount} / {totalCount}</b> 个任务</small>
					<a className="toggle" onClick={this.onToggle}>{toggleLabel}</a>
				</div>
				<ul>
					{tasks.map((t) => {
						return (
							<Item 
								task={t} 
								key={'task-' + t.get('id')}
								onClick={this.pop}
								onCheck={this.onCheck}
							/>
						)
					})}
				</ul>
			</div>
		)
	},
	findChildTasks() {
		const {
			tree,
			task
		} = this.props;

		const rootId = task.get('id');

		return findTasks(tree, rootId);
	},
	pop(node) {
		const dispatch = this.props.dispatch;
		dispatch('flowy/pop', {
			id: node.get('id')
		})
	},
	onCheck(task) {
		const {
			dispatch 
		} = this.props;

		let progress = 100;
		if (task.get('progress') === 100) {
			progress = 0;
		}

		dispatch('flowy/item/update/property', {
			node: task,
			property: 'progress',
			value: progress
		});
	},
	onToggle() {
		this.setState({
			'hideCompleted': !this.state.hideCompleted
		});
	}
});

function Item(props) {
	const task = props.task;
	const completed = task.get('progress') === 100;

	let $progress = null;
	if (!completed) {
		$progress = (
			<div 
				className={`progress-bar}`} 
				style={{width: `${task.get('progress') | 0}%`}}>
			</div>
		) 
	}

	return (
		<li className={`task-item ${completed? 'completed' : ''}`} >
			<div className="block">
				<div className="block shrink task-item__control center">
					<TaskChecker 
						checked={completed}
						onClick={()=> {
							props.onCheck(task);
						}}
						/>
				</div>
				<div className="block content task-item__main">
					<span 
						className="task-item__content" 
						onClick={(ev)=>{
							props.onClick(task);
						}}
					>
						{task.get('content')}
					</span>
					<span className="task-item__meta">
						<Circle/>
					</span>
				</div>
			</div>
			{$progress}
		</li>
	)
}

function Circle() {
	return (
		<span className="circle" style={{color: 'red'}}>
		</span>
	)
}

export default TodoList;


