import FlowyTree from './FlowyTree';


/**
 * [findTasks description]
 * @param  {[type]} tree   [description]
 * @param  {[type]} tasky  [description]
 * @param  {[type]} rootId [description]
 * @return {[type]}        [description]
 */
export default function findTasks(tree, rootId, includeRoot) {
	const dfs = FlowyTree.dfs;
	let tasks = [];

	dfs(tree, rootId, (node) => {
		const nodeId = node.get('id');
		if (nodeId === rootId) {
			if (includeRoot) {
				tasks.push(node);
			}
		} else {
			tasks.push(node);
		}
		return true;
	});

	return tasks;
}


