import xFetch from '../common/xFetch';
import {debounce} from 'lodash';

// const storage = require('electron-json-storage');
const storage = window.store;

const STORAGE_KEY = 'angrytask-tree';
const getInitialTree = function() {
	return {
		rootId: 'root',
		tree: {
			root: {
				id: 'root',
				content: 'root',
				open: true,
				children: null
			}
		}
	}
}

/**
 * [getFlowy description]
 * @return {[type]} [description]
 */
function getFlowyFromLocalStorage() {
	return new Promise(function(resolve, reject) {
		// storage.get(STORAGE_KEY, function(data) {
		// 	if (!data) {
		// 		data = getInitialTree();
		// 		storage.set(STORAGE_KEY, data, function() {
		// 			resolve(data);
		// 		});
		// 	} else {
		// 		resolve(data);
		// 	}
		// });
		let rawData = storage.get(STORAGE_KEY);
		let data;

		if (!rawData) {
			data = getInitialTree();
			rawData = JSON.stringify(data);
			storage.set(STORAGE_KEY, rawData);
		} else {
			data = JSON.parse(rawData);
		}

		resolve(data);
	})
}

function saveFlowyToLocalStorage(flowy) {
	delete flowy.treeView;
	storage.set(STORAGE_KEY, JSON.stringify(flowy));
}

function exportAppData() {
	return JSON.stringify(storage.getAll(), null, 4);
}

function importAppData(data) {
	for(let attr in data) {
		storage.set(attr, data[attr]);
	}
}

export default {
	getFlowy: getFlowyFromLocalStorage,
	saveFlowy: debounce(saveFlowyToLocalStorage, 100),
	exportAppData,
	importAppData
};


