import React, { PropTypes } from 'react';
import BigCalendar from 'react-big-calendar';
import {getEvents} from './events';
import moment from 'moment';
moment.locale('zh-cn');

BigCalendar.setLocalizer(
  BigCalendar.momentLocalizer(moment)
);

const Basic = React.createClass({
  render(){
    return (
      <BigCalendar
        {...this.props}
        events={getEvents(this.props)}
        defaultDate={getDefaultDate(this.props)}
        onSelectEvent={this.onSelectEvent}
      />
    )
  },
  onSelectEvent(event) {
    const dispatch = this.props.dispatch;
    dispatch('tasky/changeTab', {
      tab: 'TabTask'
    });

    dispatch('flowy/pop', {
      id: event.id
    })
  }
})

function getDefaultDate(props) {
  const flowy = props.flowy;
  const focusedId = flowy.get('focusedId');
  const rootId = flowy.get('rootId');
  const tree = flowy.get('tree');
  const rootNode = tree.get(focusedId) || tree.get(rootId);
  const tasky = props.tasky;
  let ret = new Date();
  const startTime = rootNode.get('planStartTime')
  if (startTime) {
    ret = moment(startTime, 'yyyy-MM-DD HH:mm:ss')._d
  }

  return ret;
} 


export default Basic;


