/**
 * rootReducer
 */
import { createReducer, combineReducers} from 'redux-immutablejs'

import flowy from './flowy';
import tasky from './tasky';

/**
 * [rootReducer description]
 * @type {[type]}
 */
const rootReducer = combineReducers({
	flowy,
	tasky
});

export default rootReducer;


