import React from "react";
import TodoList from "./TodoList";
import TaskDetail from "./TaskDetail";
import { Icon, Tag, Radio, Row, Col, Switch, Tooltip } from "antd";
import ClassNames from "classnames";

const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;

const TabTask = React.createClass({
	flatProps() {
		const props = this.props;
		const flowy = props.flowy;
		const focusedId = flowy.get("focusedId");
		const rootId = flowy.get("rootId");

		const tree = flowy.get("tree");
		const task = tree.get(focusedId) || tree.get(rootId);

		return {
			task,
			tree
		};
	},
	render() {
		const newProps = this.flatProps();
		return (
			<div className="tasky-tab-task">
				<TaskDetail {...this.props} {...newProps} />
				<TodoList {...this.props} {...newProps}/>
			</div>
		);
	},
	/**
	 * [onSwitch description]
	 * @return {[type]} [description]
	 */
	onSwitch() {
		const { task } = this.flatProps();
		setTimeout(() => {
			this.props.dispatch("tasky/create", {
				node: task
			});
		}, 300);
	}
});

export default TabTask;



