import React from "react";
import classNames from "classnames";
import { Button, Radio, Input, Icon, Select, Menu, Dropdown } from "antd";

const ButtonGroup = Button.Group;
const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;
const InputGroup = Input.Group;
const Option = Select.Option;

const LeftHeader = React.createClass({
  getInitialState() {
    return {
      focus: false
    };
  },
  render() {
    const value = this.props.flowy.get("search") || "";
    const btnCls = classNames({
      "ant-search-btn": true,
      "ant-search-btn-noempty": !!value.trim()
    });
    const searchCls = classNames({
      "ant-search-input": true,
      "ant-search-input-focus": this.state.focus
    });

    const placeholder = "搜索";
    const size = "small";
    return (
      <div className="app-left-header block shrink content">
        <div className="drag-region" />
        <div className="ant-search-input-wrapper">
          <InputGroup className={searchCls}>
            <Input
              placeholder={placeholder}
              value={value}
              onChange={this.handleInputChange}
              onFocus={this.handleFocusBlur}
              onBlur={this.handleFocusBlur}
              onPressEnter={this.handleSearch}
            />

            <div className="ant-input-group-wrap">
              <Button icon="search" className={btnCls} size={size} onClick={this.handleSearch} />
            </div>

          </InputGroup>
        </div>
      </div>
    );
  },
  handleInputChange(e) {
    // debunce
    this.props.dispatch("flowy/search", {
      keyword: e.target.value
    })
  },
  handleFocusBlur(e) {
    this.setState({
      focus: e.target === document.activeElement
    });
  },
  handleSearch(e) {
    // this.props.dispatch("flowy/search", {
    //   keyword: this.props
    // })
  }
});

export default LeftHeader;



