import React from 'react';
import ClassNames from 'classnames'
import {Icon} from 'antd';

const TaskyChecker = React.createClass({
	render() {
		const props = this.props;
		const type = this.props.type;
		const className = ClassNames('tasky-checkbox', type);
		let $checker = null;
		if (props.checked) {
			$checker = <Icon type="check"/>
		}
		return (
			<span className={className} onClick={this.props.onClick}> 
				{$checker}
			</span>
		)
	}
});

export default TaskyChecker;


