import React, { Component } from 'react';
import Editor, {createEditorStateWithText} from 'draft-js-plugins-editor';
import createCompositeDecorator from 'draft-js-plugins-editor/lib/utils/createCompositeDecorator';
import { EditorState, ContentState, convertFromRaw, convertToRaw } from 'draft-js';
import _ from 'lodash';

import Immutable , {List} from 'immutable';

// import createHashtagplugin from 'draft-js-hashtag-plugin'
import createHashtagplugin from './plugins/hashtag';
import createInlineplugin from './plugins/inline';
import createCheckedTaskplugin from './plugins/checkedTask';
import {focusTo} from './plugins/utils/rangy';

const createplugins = (editor, props) => {
	let config = _.assign({}, props || editor.props, {
		editor: editor,
		onReturn: editor.onReturn,
		onDelete: editor.onDelete 
	});
	return [
		createHashtagplugin(config),
		createInlineplugin(config)
	]
}

export default class FlowyEditor extends Component {
	constructor(props) {
		super(props);

		const onChangeHandler = _.debounce((value) => {
			this.props.onChange(this.props.task, value);
		}, 100);

		this.onChange = (editorState) => {
			const value = editorState.getCurrentContent().getPlainText();
			const lastValue = this.state.editorState.getCurrentContent().getPlainText();
			this.setState({editorState});
			if (value !== lastValue) {
				onChangeHandler(value);
			}
		}

		this.onDelete = () => {
			this.props.onDelete(this.props.task);
		}

		this.onCheck = (checked) => {
			this.props.onCheck(this.props.task, checked);
		}

		this.focus = () => this.refs.editor.focus();

		this.onReturn = () => {
			this.props.newLine(this.props.task);
		}

		this.state = { 
			editorState: createEditorStateWithText(props.task.get('content') || ''),
			plugins: createplugins(this)
		}

		this.onFocus = this.onFocus.bind(this);
		this.onTab = this.onTab.bind(this);
		this.onKeyDown = this.onKeyDown.bind(this);
	}

	componentWillReceiveProps(nextProps) {
		if (!Immutable.is(nextProps.task, this.props.task)) {
			const content = nextProps.task.get('content');
			const currentContent = this.state.editorState.getCurrentContent().getPlainText();

			if (content === currentContent) {
				return;
			}

			let editorState = createEditorStateWithText(nextProps.task.get('content'))

			let plugins = createplugins(this, nextProps);
			let _plugins = List(plugins)
		      .filter((plugin) => plugin.pluginProps !== undefined)
		      .map((plugin) => plugin.pluginProps)
		      .toArray();

		    const compositeDecorator = createCompositeDecorator(_plugins, () => this.state.editorState, this.onChange);
		    editorState = EditorState.set(editorState, { decorator: compositeDecorator });
			this.setState({ 
				editorState: editorState,
				plugins: plugins
			})
		}
	}

	shouldComponentUpdate(nextProps, nextState) {
		if (Immutable.is(this.state.editorState, nextState.editorState)) {
			return false;
		}
		return true;
	}

	render() {
		return  (
			<div 
				className="task-editor" 
				onClick={ this.focus }
				onKeyDown={this.onKeyDown}
				onFocus={this.onFocus}>
				<Editor
					editorState={this.state.editorState}
					onChange={this.onChange}
					plugins={this.state.plugins}
					onTab={this.onTab}
					ref="editor"
				/>
			</div>
		)
	}

	onFocus() {
		this.props.onFocus();
	}

	onTab(ev) {
		ev.preventDefault();
		if (ev.shiftKey) {
			this.props.shiftTab();
		} else {
			this.props.tab();
		}
	}

	onKeyDown(ev) {
		let matched = false;
		// @todo 
		// command + return -> pop
		// command + ] 打开
		// command + [ 折叠
		if (ev.metaKey || ev.ctrlKey) {
			switch (ev.keyCode) {
				case 38:
					this.props.moveUp();
					matched = true;
					break;
				case 40:
					this.props.moveDown();
					matched = true;
					break;
				default:
					break;
			}
		} else {
			switch (ev.keyCode) {
				case 38:
					this.props.focusUp();
					matched = true;
					break;
				case 40:
					this.props.focusDown();
					matched = true;
					break;
				default:
					break;
			}
		}

		if (matched) {
			ev.preventDefault();
		}

		// console.log(ev.ctrlKey, ev.keyCode, ev);
	}
}

export {focusTo, convertFromRaw, convertToRaw}


