// import xFetch from 'fetch';
import {debounce} from 'lodash';

// const storage = require('electron-json-storage');
const storage = window.store;
const STORAGE_KEY = 'angrytask-addon-tasky';

const getInitialData = function() {
	return {
		todos: {}
	}
}

function getFromLocalStorage() {
	return new Promise(function(resolve, reject) {
		// storage.get(STORAGE_KEY, function(data) {
			// if (!data) {
			// 	data = getInitialData();
			// 	storage.set(STORAGE_KEY, data, function() {
			// 		resolve(data);
			// 	});
			// } else {
			// 	resolve(data);
			// }
		// });
		let rawData = storage.get(STORAGE_KEY);
		let data;

		if (!rawData) {
			data = getInitialData();
			rawData = JSON.stringify(data);
			storage.set(STORAGE_KEY, rawData);
		} else {
			data = JSON.parse(rawData);
		}

		resolve(data);
	})
}

function saveToLocalStorage(data) {
	storage.set(STORAGE_KEY, JSON.stringify(data));
}

export default {
	get: getFromLocalStorage,
	save: debounce(saveToLocalStorage, 100)
};


