import React from 'react'
import FlowyTree from '../../common/FlowyTree';
import {Icon} from 'antd';

const Bread = React.createClass({
	render() {
		const flowy = this.props.flowy;
		const path = FlowyTree.getPath(flowy.get('tree'), flowy.get('rootId')).reverse();
		if (path.length <= 1) {
			return <div></div>
		}
		return (
			<div className="workflowy-navigation">
				<div className="inner">
					{this.renderPath(path)}
				</div>
			</div>
		)
	},
	renderPath(path) {
		const rootId = this.props.flowy.get('rootId');
		return path.map(it => {
			const id = it.get('id');
			const isRoot = id === rootId;
			if (isRoot) {
				return null;
			}
			return(
				<span key={`path-${id}`} className="breadcrumb-item">
					<span onClick={this.pop.bind(null, id)} >
						{it.get('content')}
					</span>
				</span>
			);
		})
	},
	pop(id) {
		const node = this.props.node;
		const dispatch = this.props.dispatch;
		
		dispatch('flowy/pop', {
			id
		});
	}
})

export default Bread;


